body{
    background-color: rgb(71, 6, 71);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}
h1,h2,h3,h4,h5,h6{
    text-transform: capitalize;
}
main{
    background-color: #fff;
    width: 99%;
    height:auto;
    margin: 50px auto;
    padding: 1px 30px 30px 30px;
    box-sizing: border-box;
    /* text-align: center; */
}
.title{
    text-align: center;
}
.underline{
    width: 15%;
    height:3px;
    margin:-15px auto 5px auto;
    background: gold;
}
article{
    width: 50%;
    margin: 0px auto;
}
img{
    width: 100%;
    margin: 0 0 0 0;
    border: 5px solid goldenrod;
}
header{
    display: flex;
    justify-content: space-between;
    padding: 0;
    border-bottom: 1px solid grey;
}
header>h4{
    margin-bottom: 5px;
}
h4{
    margin-top: 0;
}
h4:nth-child(even){
    color: goldenrod
}
p.item-text{
    text-align: justify;
    text-align-last:center;
}
@media screen and (min-width:760px) {
    main{
        height:1000px;
    }
    article{
        width: 45%;
        float: left;
        margin: 0 auto 40px 3%;
    }
    img{
        width: 35%;
        float:left;
    }

    .item-info{
        width: 55%;
        float: left;
        margin-left: 10px;
    }
}
.btn-container{
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    gap: 8px;
}

.question-container{
    display: flex;
    justify-content: space-between;
}
.questions > p:nth-child(odd){
    font-weight: bold;
    color: #333;
}
.btn-container{
    align-self:center;
}
button{
    font-weight: bold;
    border: 0;
    padding: 5px 5px 2px 5px;
    background-color: rgb(228, 222, 222);
}
button:hover{
    background-color: rgb(172, 166, 166);
}
button:active{
    background-color: rgb(228, 222, 222);
}
button:focus{
    background-color: white;
}
.btn-icon{
    color: red;
    font-weight: bold;
}